.settings {
  text-align: left;
  margin: auto;
  width: 75%;
}

Form {
  padding: 1rem;
  margin: 5px;
  background-image: linear-gradient(to bottom right, #b9bfc6, #abb3ba);
  border: 1px solid #505050;
}

.description-text {
  display: inline-block;
  width: 75%;
  font-size: 1.2rem;
}

.generate-button {
  box-shadow: 3px 3px #b9bfc6;
}

.settings-button {
  margin-top: 2em;
  margin-bottom: 1em;
  box-shadow: 3px 3px #b9bfc6;
}

.multiple-result {
  font-size: 2em;
}

.sequences-input {
  width: 75%;
  height: 200px;
  overflow: auto;
}

.result-background {
  border: 1px solid #505050;
  padding: 1rem;
  margin-bottom: 2em;
  background-image: linear-gradient(to bottom right, #e3e6e8, #b9bfc6);
}

.result-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: baseline;
  width: 75%;
  text-align: center;
  margin: 3em auto;
}

.result-previous {
  flex: 1;
  opacity: 50%;
  text-align: center;
  padding: 1rem;
}

.result {
  flex: 1;
  text-align: center;
  padding: 1rem;
}

.chord-grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 4fr));
}

#tooltip-color {
  font-size: 1em;
  width: 200px;
}

#result-numbers-prev {
  font-size: 4em;
}

#result-color {
  width: 150px;
  height: 150px;
  display: inline-block;
  border: 2px solid black;
  margin: 0.25em;
}

#result-chord {
  display: inline-block;
  font-size: 4rem;
  word-wrap: break-word;
  border: 1px solid grey;
  box-shadow: 3px 2px #505050;
  padding: 0.5rem;
  margin: 1rem;
}

#result-numbers {
  font-size: 6rem;
  word-wrap: break-word;
  width: 100%;
}

#result-string {
  font-size: 6rem;
  word-wrap: break-word;
  width: 100%;
  font-weight: bold;
}

#result-element {
  font-size: 6rem;
  word-wrap: break-word;
  width: 100%;
}

#result-image {
  width: 30rem;
  border: 1px black solid;
  box-shadow: 5px 5px #999999;
}

.result-num-animation {
  animation-name: num-animate;
  animation-duration: 1s;
}

.result-previous-animation {
  animation-name: num-prev-animate;
  animation-duration: 1s;
}

.result-color-animation-show {
  animation-name: color-animate-show;
  animation-duration: 1s;
}

.result-color-animation-change {
  animation-name: color-animate-change;
  animation-duration: 1s;
}

.result-string-animation {
  animation-name: string-animate;
  animation-duration: 1s;
}

.result-chord-animation-show {
  animation-name: chord-animate-show;
  animation-duration: 1s;
}

.result-chord-animation-change {
  animation-name: chord-animate-change;
  animation-duration: 1s;
}

/* removing margin animation due to no previous result */
@keyframes num-animate {
  from {
    color: #ffffff;
    /*margin-right: -50%;*/
  }
  to {
    color: black;
    /* margin-right: 0px; */
  }
}

@keyframes num-prev-animate {
  from {
    color: #ffffff;
    margin-right: -50%;
  }
  to {
    color: #000000;
    margin-right: 0px;
  }
}

@keyframes color-animate-show {
  from {
    width: 10px;
    height: 10px;
  }
  to {
    width: 150px;
    height: 150px;
  }
}

@keyframes color-animate-change {
  0% {
    width: 150px;
    height: 150px;
  }
  50% {
    width: 160px;
    height: 160px;
  }
  100% {
    width: 150px;
    height: 150px;
  }
}

@keyframes string-animate {
  from {
    color: #ffffff;
  }
  to {
    color: #000000;
  }
}

@keyframes chord-animate-show {
  from {
    font-size: 0rem;
  }
  to {
    font-size: 4rem;
  }
}

@keyframes chord-animate-change {
  0% {
    font-size: 4rem;
    border: 2px solid rgb(241, 241, 241);
    box-shadow: 5px 3px #000000;
  }
  50% {
    font-size: 4.2rem;
    border: 2px solid rgb(241, 241, 241);
    box-shadow: 5px 3px #000000;
  }
  100% {
    font-size: 4rem;
    border: 1px solid grey;
    box-shadow: 3px 2px #505050;
  }
}
